.navbar {
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .navbar-logo {
    width: 111px;
  }
  ul {
    display: flex;
    padding: 0;
    margin: 0;
    list-style-type: none;
    font-weight: 500;
    li {
      a {
        padding: 13px 20px;
      }
    }
  }
}

